#navigation {
  background: rgba(0, 0, 0, 0.9);
  position: fixed;
  z-index: 9999;
  width: 100%;
}
#navigation .navigation {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-between;
  align-items: center;
  padding-top: 15px;
  padding-bottom: 15px;
}
@media screen and (max-width: 780px) {
  #navigation .navigation .navigation__list {
    display: flex;
    flex-direction: column;
    position: absolute;
    text-align: center;
    top: -500%;
    width: 100%;
    height: 100vh;
  }
  #navigation .navigation .navigation__list_item {
    width: 100%;
    padding: 15px;
    border-bottom: 1px solid #000;
    background: rgba(0, 0, 0, 0.9);
  }
  #navigation .navigation .navigation__list_item:hover {
    background: #000;
  }
  #navigation .navigation .navigation__list_item:active {
    background: #000;
  }
  #navigation .navigation .navigation__burger {
    display: block;
  }
}
#navigation .navigation__logo {
  width: 20%;
  max-width: 133px;
}
#navigation .navigation__list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}
#navigation .navigation__list_item {
  margin: 0 40px 0 0;
  color: #fff;
  font-size: 1rem;
}
#navigation .navigation__list_item:hover {
  color: #D6D6D6;
}
#navigation .navigation__list_item:active {
  color: #D6D6D6;
}
#navigation .navigation__list_item a {
  color: inherit;
  text-decoration: none;
}
#navigation .navigation__burger {
  display: none;
  position: relative;
  margin: 0;
  padding: 0;
  width: 55px;
  height: 20px;
  font-size: 0;
  text-indent: -9999px;
  appearance: none;
  box-shadow: none;
  border-radius: none;
  border: none;
  cursor: pointer;
}
#navigation .navigation__burger:focus {
  outline: none;
}
#navigation .navigation__burger span {
  display: block;
  position: absolute;
  top: 10px;
  left: 18px;
  right: 18px;
  height: 2px;
  background: #fff;
}
#navigation .navigation__burger span::before {
  position: absolute;
  display: block;
  left: 0px;
  width: 100%;
  height: 2px;
  background-color: #fff;
  content: "";
  top: -5px;
  transform-origin: top right;
}
#navigation .navigation__burger span::after {
  position: absolute;
  display: block;
  left: 0px;
  width: 100%;
  height: 2px;
  background-color: #fff;
  content: "";
  transform-origin: top right;
  bottom: -5px;
  transform-origin: bottom right;
}
#navigation .navigation__burger.is-active span {
  transform: rotate(-180deg);
}
#navigation .navigation__burger.is-active span::before {
  width: 50%;
  transform: translateX(-9px) translateY(7px) rotate(230deg);
}
#navigation .navigation__burger.is-active span::after {
  width: 50%;
  transform: translateX(-9px) translateY(-7px) rotate(135deg);
}
