}
#footer {
  background: rgba(0, 0, 0, 0.9);
}
#footer .footer {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  font-size: 1rem;
  line-height: 1;
  justify-content: space-between;
  color: #fff;
  padding-top: 25px;
  padding-bottom: 25px;
}
@media screen and (max-width: 780px) {
  #footer .footer {
    flex-direction: column;
    text-align: center;
  }
  #footer .footer .footer__copyright {
    margin-bottom: 30px;
  }
}
#footer .footer__social_links {
  vertical-align: middle;
}
#footer .footer a {
  font-size: 1.2rem;
  display: inline-block;
  vertical-align: middle;
  color: #fff;
  margin: 5px;
}
#footer .footer a.icon-vk:hover {
  color: #4b74a5;
}
#footer .footer a.icon-instagram:hover {
  background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
}
